.project-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 3.5rem;

    &:hover {
        .project-card__image {
            transform: scale(1.2);
        }
    }
}

.project-card__cover {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin-bottom: 1rem;
    width: 315px;
    height: 375px;
    overflow: hidden;

    @media(min-width: $screen-lg) {
        margin-bottom: 3rem;
        width: 376px;
        height: 448px;
    }

    @media(min-width: $screen-xl) {
        width: 470px;
        height: 560px;
    }
}

.project-card__image {
    width: 100%;
    transition: all .2s ease-in-out;
}

.project-card__shadow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: inset 2px 2px 8px -1px rgba($black, 0.1);
}

.project-card__name {
    @include font-size(15px);

    margin-bottom: 0.5rem;
    font-weight: $font-weight-semibold;

    @media(min-width: $screen-lg) {
        @include font-size(18px);
    }
}

.project-card__type {
    @include font-size(15px);

    @media(min-width: $screen-lg) {
        @include font-size(18px);
    }
}
