// Floating elements
@keyframes float {
    0% {
        transform: translatey(0);
    }

    50% {
        transform: translatey(25px);
    }

    100% {
        transform: translatey(0);
    }
}

@keyframes float-invested {
    0% {
        transform: translatey(25px);
    }

    50% {
        transform: translatey(0);
    }

    100% {
        transform: translatey(25px);
    }
}
